.container{
    display:flex;
    flex-direction: row;
    gap: 15px;
}
.left{
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.header{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items:flex-start;
    gap: 10px;
    padding: 10px;

    background: #FFFFFF;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
    border-radius: 5px
}
.productList{
    display:flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;

    background: #FFFFFF;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
    border-radius: 5px
}