.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    border-radius: 5px;
    margin: 10px;
    padding: 10px;

}
.header{
    text-align: center;
    margin: 0;
 }
 .errorMessage{
    background-color:rgb(255, 88, 88);
    border-radius: 10px;
    text-align: center;
    padding: 5px;
}
.form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  .form input{
    width: 220px;
  }
  .fieldContainer{
    display:flex;
    flex-direction: column;
  }
  .formErrorContainer{
    color: red;
    font-size: 12px;
  }